import tw from 'twin.macro';
import styled from 'styled-components';
import { Dropdown } from 'primereact/dropdown';
import { useState } from 'react';

const Styles = {
  TopNav: styled.div`
    ${tw`h-[75px] flex justify-between items-center px-[25px]`};

    .p-inputtext {
      ${tw`p-0 h-[34px] flex items-center`};
    }
  `,
};

interface Country {
  name: string;
  code: string;
}

export default function TopNav() {
  const [selectedExchange, setSelectedExchange] = useState(null);
  const countries = [
    { name: '일본 1엔', code: 'JP' },
    { name: '스페인 1', code: 'ES' },
    { name: '미국 1USD', code: 'US' },
  ];

  const selectedCountryTemplate = (option: Country, props: any) => {
    if (option) {
      return (
        <div className="flex align-items-center">
          <img
            alt={option.name}
            src="https://primefaces.org/cdn/primereact/images/flag/flag_placeholder.png"
            className={`mr-2 flag flag-${option.code.toLowerCase()}`}
            style={{ width: '18px' }}
          />
          <div>{option.name}</div>
        </div>
      );
    }

    return <span>{props.placeholder}</span>;
  };

  const countryOptionTemplate = (option: Country) => {
    return (
      <div className="flex align-items-center">
        <img
          alt={option.name}
          src="https://primefaces.org/cdn/primereact/images/flag/flag_placeholder.png"
          className={`mr-2 flag flag-${option.code.toLowerCase()}`}
          style={{ width: '18px' }}
        />
        <div>{option.name}</div>
      </div>
    );
  };

  return (
    <Styles.TopNav>
      <div className="w-[297px]">
        <Dropdown
          value={selectedExchange}
          onChange={(e) => setSelectedExchange(e.value)}
          options={countries}
          optionLabel="name"
          placeholder="Select a Country"
          valueTemplate={selectedCountryTemplate}
          itemTemplate={countryOptionTemplate}
          className="w-full md:w-14rem"
        />
      </div>
      <div>TopNav2</div>
    </Styles.TopNav>
  );
}
