import React from 'react';
import styled from 'styled-components';
import tw from 'twin.macro';

const Styles = {
  Checkbox: styled.div`
    ${tw`inline-flex gap-[10px]`}

    & + & {
      /* ${tw`ml-[100px]`} */
    }
    & > * {
      ${tw`cursor-pointer`}
    }
  `,
};

/**
 * id: input, label 아이디
 * value: input 값
 * labelTxt: 라벨 텍스트
 */
interface ICheckboxProps {
  id: string;
  value: string;
  labelTxt: string;
}

/**
 * 라벨과 연결된 라디오 버튼 컴포넌트
 * @param {id, value, labelTxt, ...rest} ICheckboxProps 참조
 * @return 라벨+라디오 버튼 컴포넌트 반환
 */
export default React.forwardRef(function Checkbox(
  { id, value, labelTxt, ...rest }: ICheckboxProps,
  ref: React.LegacyRef<HTMLInputElement> | undefined,
) {
  return (
    <Styles.Checkbox>
      <input type="checkbox" value={value} id={id} {...rest} ref={ref} />
      <label htmlFor={id}>{labelTxt}</label>
    </Styles.Checkbox>
  );
});
