interface IInfoBoxWrapProps {
  children: React.ReactNode;
}
const InfoBoxWrap = ({ children }: IInfoBoxWrapProps) => {
  return <Styles.InfoBoxWrap>{children}</Styles.InfoBoxWrap>;
};

// 달력에서 일정을 눌렀을때 id를 통해 api로 데이터 가져와서 있으면 기존데이터 매핑 없으면 빈값
// 저장 = 수정/ 팝업닫기 / 기존데이터 / 인풋데이터를 컨트롤하기 위한 핸들러
interface IScheduleRegisterProps {
  handleSaveForm: () => void;
  handleCloseForm: () => void;
}
const ScheduleRegister = ({
  handleSaveForm,
  handleCloseForm,
}: IScheduleRegisterProps) => {
  const { register, handleSubmit, getValues } = useForm({
    defaultValues: new dtoScheduleRegister(),
  });
  const [weddingDate, setWeddingDate] =
    useState<Nullable<string | Date | Date[]>>(null);
  const [travelDate, setTravelDate] =
    useState<Nullable<string | Date | Date[]>>(null);
  const [visitDate, setVisitDate] =
    useState<Nullable<string | Date | Date[]>>(null);

  return (
    <Styles.ScheduleRegister>
      <div className="date-info">[2022-11-01] 월요일 AM 10:30</div>
      <form
        onSubmit={handleSubmit((data) => {
          console.log('data', data);
          handleSaveForm();
        })}
      >
        <div className="info-box">
          <InfoBoxWrap>
            <div className="form-row">
              <FormLabel labelTxt="상태">
                <Radio
                  id="schedule"
                  value="schedule"
                  labelTxt="일정"
                  {...register('status')}
                />
                <Radio
                  id="meeting"
                  value="meeting"
                  labelTxt="미팅"
                  {...register('status')}
                />
                <Radio
                  id="business"
                  value="business"
                  labelTxt="출장"
                  {...register('status')}
                />
              </FormLabel>
            </div>
            <div className="form-row">
              <FormLabel labelTxt="여행형태">
                <Radio
                  id="honeymoon"
                  value="honeymoon"
                  labelTxt="허니문"
                  {...register('travelType')}
                />
                <Radio
                  id="travel"
                  value="travel"
                  labelTxt="일반여행"
                  {...register('travelType')}
                />
              </FormLabel>
            </div>
            <div className="form-row">
              <FormLabel labelTxt="성함">
                <input type="text" {...register('name')} placeholder="성함" />
              </FormLabel>
              <FormLabel labelTxt="연락처">
                <input
                  type="text"
                  {...register('phoneNumber')}
                  placeholder="010-xxxx-xxxx"
                />
              </FormLabel>
            </div>
            <div className="form-row">
              <FormLabel labelTxt="예식일/지역">
                <Calendar
                  value={weddingDate}
                  onChange={(e) => setWeddingDate(e.value)}
                  showButtonBar
                />
                <select {...register('weddingPlace')}>
                  <option value="">Select...</option>
                  <option value="A">Category A</option>
                  <option value="B">Category B</option>
                </select>
              </FormLabel>
            </div>
            <div className="form-row">
              <FormLabel labelTxt="여행기간">
                <Calendar
                  value={travelDate}
                  onChange={(e) => setTravelDate(e.value)}
                  selectionMode="range"
                  readOnlyInput
                />
              </FormLabel>
            </div>
            <div className="form-row">
              <FormLabel labelTxt="여행지">
                <input type="text" {...register('travelPlace')} />
              </FormLabel>
              <FormLabel labelTxt="방문경로">
                <select {...register('howVisit')}>
                  <option value="">Select...</option>
                  <option value="A">Category A</option>
                  <option value="B">Category B</option>
                </select>
              </FormLabel>
            </div>
            <div className="form-row">
              <FormLabel labelTxt="여행인원">
                <select {...register('peopleNumber')}>
                  <option value="">Select...</option>
                  <option value="1">1</option>
                  <option value="2">2</option>
                </select>
                <span>명</span>
              </FormLabel>
            </div>
            <div className="form-row">
              <FormLabel labelTxt="문의사항" height="72px">
                <textarea cols={30} {...register('qs')} />
              </FormLabel>
            </div>
          </InfoBoxWrap>
          <InfoBoxWrap>
            <div className="form-row">
              <FormLabel labelTxt="방문일정">
                <Calendar
                  value={visitDate}
                  onChange={(e) => setVisitDate(e.value)}
                  showTime
                  hourFormat="12"
                />
              </FormLabel>
            </div>
            <div className="form-row">
              <FormLabel labelTxt="담당자">
                <select {...register('bookingAgent')}>
                  <option value="">Select...</option>
                  <option value="김철수">김철수</option>
                  <option value="2">2</option>
                </select>
                <Checkbox
                  id="notice"
                  value="true"
                  labelTxt="전체공지"
                  {...register('notice')}
                />
              </FormLabel>
            </div>
            <div className="form-row">
              <FormLabel labelTxt="설명" height="72px">
                <textarea cols={30} {...register('desc')} />
              </FormLabel>
            </div>
          </InfoBoxWrap>
        </div>
        <div className="info-controler">
          <input type="button" value="취소" onClick={handleCloseForm} />
          <input type="submit" value="저장" />
        </div>
      </form>
    </Styles.ScheduleRegister>
  );
};

