function renderEventContent(eventContent: EventContentArg) {
  return (
    <>
      <b>{eventContent.timeText}</b>
      <i>{eventContent.event.title}</i>
    </>
  );
}
// event 리스트
function renderSidebarEvent(event: EventApi) {
  return (
    <li key={event.id}>
      <b>
        {formatDate(event.start as Date, {
          year: 'numeric',
          month: 'short',
          day: 'numeric',
        })}
      </b>
      <i>{event.title}</i>
    </li>
  );
}

export default function Schedule() {
  // 상태
  // 전체 페이지 데이터 상태 (월 기준)-> react-query
  const ScheduleData = dummyData;
  // 팝업 종류 상태
  const [popupVisible, setPopupVisible] = useState(false);
  // 선택 탭 상태
  const { TabButtons, selectedButtonState } = useTabButtons<TTabKey>({
    tabButtons: [
      {
        key: 'consult',
        cont: '상담스케쥴',
      },
      {
        key: 'booking',
        cont: '예약건',
      },
      {
        key: 'departure',
        cont: '출발건',
      },
      {
        key: 'cancelled',
        cont: '취소일',
      },
      {
        key: 'company',
        cont: '회사일정',
      },
      {
        key: 'individual',
        cont: '개인일정',
      },
    ],
    baseStyle: tw`px-[20px] h-[40px] text-[14px] flex items-center rounded-[8px] text-black bg-[#fff] border-[1px] border-black border-solid`,
    activeStyle: tw`bg-black text-[#fff] border-black`,
  });

  // 이벤트 핸들러
  // 달력에 새로운 일저을 추가할때. 클릭 -> 팝업을 띄워주기(tab 선택기준으로)
  const handleDateSelect = () => {
    setPopupVisible(true);
    console.log('일자');
  };

  // 달력 이벤트 요소 클릭 -> api로 데이터 가져오기 -> 팝업을 띄워주기(tab 선택기준으로)
  const handleEventClick = (clickInfo: EventClickArg) => {
    console.log('이벤트 요소 클릭');
  };
  // 탭 선택
  // form 값에 대한 핸들러는 react-hook-form으로 처리

  // 빈 달력 일자 클릭시 선택 탭을 기준으로 팝업 종류 분기 -> 상담스케줄, 회사일정, 개인일정 : 일정등록 / 예약건 : 예약등록 / 나머지는 출발, 취소는??
  const switchPopupCont = (selectedButtonState: TTabKey) => {
    switch (selectedButtonState) {
      case 'consult':
      case 'company':
      case 'individual':
        // 일정등록
        return (
          <ScheduleRegister
            handleSaveForm={() => {}}
            handleCloseForm={() => setPopupVisible(false)}
          />
        );
      case 'booking':
        // 예약등록
        return <BookingRegister />;
      default:
        // 취소, 출발
        return;
    }
  };

  // 선택한 탭에 해당하는 달력 데이터 필터링 함수
  const filterCalendarDataFromSchedule = (
    data: ISchedulePageCalendarDatas,
    selectedButtonState: TTabKey,
  ): Array<EventInput> => {
    return data[`${selectedButtonState}Schedule`];
  };

  // 팝업에서 데이터 업데이트하면 달력에 반영하는 로직 필요

  // api 함수
  // 데이터 위한 api : 전체 페이지 데이터 호출, 이벤트(일정) 조회,등록,수정,삭제 -> event id로 컨트롤

  return (
    <Styles.Schedule>
      {popupVisible && (
        <Popup
          onClose={() => {
            setPopupVisible(false);
          }}
        >
          {switchPopupCont(selectedButtonState as TTabKey)}
        </Popup>
      )}
      {/* data summary */}
      <div className="data-summary">
        <div className="summary-box">
          <div className="title">월 상담건</div>
          <div className="data">{ScheduleData.consultForMonth}</div>
        </div>
        <div className="summary-box">
          <div className="title">월 예약건</div>
          <div className="data">{ScheduleData.bookingForMonth}</div>
        </div>
        <div className="summary-box">
          <div className="title">월 출발건</div>
          <div className="data">{ScheduleData.departureForMonth}</div>
        </div>
      </div>
      {/* calendar data tab */}
      <div className="calendar-data-tab">
        <TabButtons />
      </div>
      <div className="monthly-data">
        <FullCalendar
          plugins={[dayGridPlugin, timeGridPlugin, interactionPlugin]}
          headerToolbar={{
            left: 'today prevYear prev',
            center: 'title',
            right: 'next nextYear dayGridMonth,timeGridWeek,timeGridDay',
          }}
          initialView="dayGridMonth"
          locale="ko"
          editable={true} // 이벤트 드래그 이동 가능여부
          selectable={true} // 달력 내부를 드래그해서 범위 이벤트 등록여부
          selectMirror={true} // selectable 옵션을 이용한 드래그 이벤트 등록할때 미리보기 느낌 기능 여부
          dayMaxEvents={true} // 등록된 이벤트 표기 제한
          events={filterCalendarDataFromSchedule(
            ScheduleData.scheduleForMonth,
            selectedButtonState as TTabKey,
          )}
          select={handleDateSelect} // 일자를 선택할때 실행
          eventClick={handleEventClick}
          // eventContent={renderEventContent} // 이벤트 요소 커스텀
          /* you can update a remote database when these fire:
            eventAdd={function(){}}
            eventChange={function(){}}
            eventRemove={function(){}}
            */
        />
      </div>
    </Styles.Schedule>
  );
}
