import tw from 'twin.macro';
import styled from 'styled-components';
import { Outlet } from 'react-router-dom';
import SideNav from '@components/layout/SideNav';
import TopNav from '@components/layout/TopNav';

const Styles = {
  Layout: styled.div`
    ${tw`flex  h-[100vh] overflow-y-scroll`};
  `,
  Sidebar: styled.div`
    ${tw`flex-none w-[246px]`}
  `,
  Content: styled.div`
    ${tw`flex-1`}
  `,
  Main: styled.main``,
};

// login 이후 접근 가능
export default function Layout() {
  return (
    <Styles.Layout>
      <Styles.Sidebar>
        <SideNav />
      </Styles.Sidebar>
      <Styles.Content>
        <TopNav />
        <Styles.Main>
          <Outlet />
        </Styles.Main>
      </Styles.Content>
    </Styles.Layout>
  );
}
