import { EventInput } from '@fullcalendar/core';

// 스케줄 관리 페이지 데이터
export interface ISchedulePageDataModel {
  consultForMonth: number;
  bookingForMonth: number;
  departureForMonth: number;
  scheduleForMonth: ISchedulePageCalendarDatas;
}

// 스케쥴 관리 페이지 내 달력 데이터
export interface ISchedulePageCalendarDatas {
  consultSchedule: Array<Partial<EventInput>>;
  bookingSchedule: Array<Partial<EventInput>>;
  departureSchedule: Array<Partial<EventInput>>;
  cancelledSchedule: Array<Partial<EventInput>>;
  companySchedule: Array<Partial<EventInput>>;
  individualSchedule: Array<Partial<EventInput>>;
}

export type TTabKey =
  | 'consult'
  | 'booking'
  | 'departure'
  | 'cancelled'
  | 'company'
  | 'individual';

// 일정등록 (ScheduleRegister) 폼 dto
export class dtoScheduleRegister {
  status = 'schedule';
  travelType = 'honeymoon';
  name = '';
  phoneNumber = '';
  weddingPlace = '';
  travelPlace = '';
  howVisit = '';
  peopleNumber = 1;
  qs = '';
  bookingAgent = '';
  notice = false;
  desc = '';
}

/**
 * 0 : 성인
 * 1 : 유아
 */
export type TCustomer = 0 | 1;

/**
 * 0 : 남
 * 1 : 여
 */
export type TGender = 0 | 1;
