import React from 'react';
import { createPortal } from 'react-dom';
import styled from 'styled-components';
import tw from 'twin.macro';

const Styles = {
  Popup: styled.div`
    ${tw`absolute left-0 right-0 top-0 bottom-0 bg-[#0000007f] z-[51] flex items-center justify-center`}
  `,
  PopupCont: styled.div`
    ${tw`relative rounded-[8px] px-[74px] py-[34px] max-w-[1100px] w-[80%] max-h-[80%] bg-[#fff] overflow-y-scroll`}

    .close-btn {
      ${tw`absolute right-0 top-0`}
    }
  `,
};

interface IPopupProps {
  children: React.ReactNode;
  onClose: () => void;
}
export default function Popup({ children, onClose }: IPopupProps) {
  const portalRef: any = document.getElementById('portal-popup');

  return createPortal(
    <Styles.Popup
      onClick={() => {
        onClose();
      }}
    >
      <Styles.PopupCont
        onClick={(e) => {
          e.stopPropagation();
        }}
      >
        {children}
        <i
          className="close-btn"
          onClick={(e) => {
            e.stopPropagation();
            onClose();
          }}
        >
          close
        </i>
      </Styles.PopupCont>
    </Styles.Popup>,
    portalRef,
  );
}
