import React, { useState } from 'react';
import tw from 'twin.macro';
import styled from 'styled-components';

const Styles = {
  TabButtons: styled.div<Omit<TabButtons<any>, 'tabButtons'>>`
    ${tw`flex gap-[4px]`}
    .tab-btn {
      ${tw`flex items-center border-none cursor-pointer`}
      ${(props) => props.baseStyle};
      &.on {
        ${(props) => props.activeStyle};
      }
    }
  `,
};

interface TabButton<TKey> {
  key: string | TKey;
  cont: string | React.ReactNode;
}
interface TabButtons<TKey> {
  tabButtons: Array<TabButton<TKey>>;
  baseStyle: any;
  activeStyle: any;
}
export default function useTabButtons<TKey>({
  tabButtons,
  baseStyle,
  activeStyle,
}: TabButtons<TKey>) {
  const [selectedButton, setSelectedButton] = useState<string | TKey>(
    tabButtons[0].key,
  );

  const onClick =
    (selectKey: string | TKey) =>
    (e: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
      setSelectedButton(selectKey);
    };

  return {
    TabButtons: () => (
      <Styles.TabButtons baseStyle={baseStyle} activeStyle={activeStyle}>
        {tabButtons.map((tabButton, idx) => {
          return (
            <button
              key={tabButton.key as string}
              className={`tab-btn ${
                selectedButton === tabButton.key ? 'on' : ''
              }`}
              onClick={onClick(tabButton.key)}
            >
              {tabButton.cont}
            </button>
          );
        })}
      </Styles.TabButtons>
    ),
    selectedButtonState: selectedButton,
  };
}
