import tw from 'twin.macro';
import styled from 'styled-components';
import { useTable } from 'react-table';
import { useMemo } from 'react';

interface IDataTableWrap {
  tableStyle?: any;
  tableHeadStyle?: any;
  tableHeadCellStyle?: any;
  tableBodyStyle?: any;
  tableRowStyle?: any;
  tableRowCellStyle?: any;
}
const Styles = {
  DataTableWrap: styled.div<any>`
    .table {
      ${tw`border-collapse border-spacing-0 w-full`}
      ${(props) => props.tableStyle};
      .table-head {
        ${tw`text-[14px] h-[35px]`}
        ${(props) => props.tableHeadStyle};
        .table-head-cell {
          border: none;
          border-bottom: 1px solid #1e1e22;
          ${(props) => props.tableHeadCellStyle};
        }
      }
      .table-body {
        ${(props) => props.tableBodyStyle};
        .table-body-row {
          ${(props) => props.tableRowStyle};
          .table-body-cell {
            ${tw`h-[48px]`}
            ${(props) => props.tableRowCellStyle};
          }
        }
      }
    }
  `,
};

interface IDataListTable {
  columnConf: any;
  dataList: Array<{
    [accessor: string]: string | React.ReactNode;
  }>;
  styles?: {
    tableStyle?: any;
    tableHeadStyle?: any;
    tableHeadCellStyle?: any;
    tableBodyStyle?: any;
    tableRowStyle?: any;
    tableRowCellStyle?: any;
  };
}

export default function DataListTable(props: IDataListTable) {
  const { getTableProps, getTableBodyProps, headerGroups, rows, prepareRow } =
    useTable({ columns: props.columnConf, data: props.dataList });

  return (
    <Styles.DataTableWrap {...props.styles}>
      <table {...getTableProps()} className="table">
        <thead className="table-head">
          {headerGroups.map((headerGroup) => (
            <tr {...headerGroup.getHeaderGroupProps()}>
              {headerGroup.headers.map((column) => (
                <th className="table-head-cell" {...column.getHeaderProps()}>
                  {column.render('Header')}
                </th>
              ))}
            </tr>
          ))}
        </thead>
        <tbody className="table-body" {...getTableBodyProps()}>
          {rows.map((row) => {
            prepareRow(row);
            return (
              <tr className="table-body-row" {...row.getRowProps()}>
                {row.cells.map((cell) => {
                  return (
                    <td className="table-body-cell" {...cell.getCellProps()}>
                      {cell.render('Cell')}
                    </td>
                  );
                })}
              </tr>
            );
          })}
        </tbody>
      </table>
    </Styles.DataTableWrap>
  );
}
